import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';
import { Popover, PopoverHeader, PopoverBody, DropdownItem, FormGroup, Collapse, Label } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap'
import { loadLocalAccount, saveUserSec, createPeerId, calcAccountId, getShareHost, getCurrentHost } from '../../ggv/js/gegeUtil'
import { Nav, NavItem, NavLink, Alert, Breadcrumb, BreadcrumbItem, ListGroup, ListGroupItem } from 'reactstrap';
import classnames from 'classnames';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import AccountDialog from '../../conponents/AccountDialog'
import PopHint from '../../conponents/PopHint/PopHint'
import BtbSecret from '../../conponents/BtbSecret/BtbSecret'
import ActionButton from '../../conponents/ActionButton/ActionButton'
import DepositDialog from '../../conponents/DepositDialog/DepositDialog'
const { loadProductList, loadProduct, doBShopTransaction, getProductStatus, loadProductDetails, loadJoinTransactions, loadIssueTransactions, shareEncrypt, getProductNextStep } = require('../eshop.js')

class ProductList extends Component {

  constructor(props) {
    super(props);
    this.deadline = 5;
    this.fee = '1';
    this.state = {
      account: {}, list: [], page: 1, joinsPage: 1, addProduct: false, join: false, detailProduct: null,
      detail: false, paying: null, activeTab: '0', detailTab: '0', joins: null, showAccount: false,
      content: '.', filterValue: null, success: false, issuePage: 1
    }
    this.modules = {
      toolbar: [
        [{ 'header': '1' }, { 'header': '2' }, { 'font': [] }],
        [{ size: [] }], [{ align: [] }, 'direction'],
        ['bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block'],
        [{ color: [] }, { background: [] }],
        [{ 'list': 'ordered' }, { 'list': 'bullet' },
        { 'indent': '-1' }, { 'indent': '+1' }],
        ['link', 'image'],
        ['clean']
      ],
      clipboard: {
        // toggle to add extra line breaks when pasting HTML:
        matchVisual: false,
      }
    }
  }

  componentDidMount() {
    var account = loadLocalAccount();
    if (account && account.secret)
      this.secret = account.secret;
    this.setState({ account })
    let hash = window.location.hash;
    let query = null;
    if (hash) {
      let idx = hash.indexOf("#", 1);
      if (idx > 0) {
        query = hash.substr(idx + 1, 40);
        idx = query.indexOf("#", 1);
        if (idx > 0) {
          query = query.substr(0, idx);
        }
        //console.log('query=' + query);
      }
    }
    setTimeout((() => {
      if (query)
        loadProduct(this.onProductList.bind(this), query)
      else
        loadProductList(this.onProductList.bind(this), 1, this.state.activeTab)
    }).bind(this), 500);
    //
    //this.doTest()
  }

  onProductList(ok, list) {
    if (ok) {
      this.setState({ list })
    } else {
      this.setState({ list: [] })
    }
  }

  prePage() {
    if (this.state.page > 1) {
      this.state.page--
      loadProductList(this.onProductList.bind(this), this.state.page, this.state.activeTab, '0', this.state.filterValue)
    }
  }

  nextPage() {
    this.state.page++
    loadProductList(this.onProductList.bind(this), this.state.page, this.state.activeTab, '0', this.state.filterValue)
  }

  filterBlocks() {
    this.setState({ page: 1 })
    loadProductList(this.onProductList.bind(this), 1, this.state.activeTab, '0', this.state.filterValue)
  }

  togglePublishProduct() {
    this.setState({
      addProduct: !this.state.addProduct, success: false
    });
  }

  toggleJoin() {
    this.setState({
      join: !this.state.join, joinsPage: 1, success: false, dialogMsg: ''
    });
    //set product to null
  }

  toggleDetail() {
    this.setState({
      detail: !this.state.detail,
      issues: null, joins: null,
      detailTab: '0'
    });
  }


  onSecret(sec) {
    if (sec) {
      this.secret = sec
      let rsId = calcAccountId(this.userSec);
      this.setState({ rsId: rsId })
    }
  }

  updateFee(e) {
    if (e && e.target) {
      this.fee = e.target.value;
      if (this.fee.length == 0) {
        this.fee = 1;
      }
    }
  }

  updatePrice(e) {
    if (e && e.target) {
      this.price = e.target.value;
    }
  }
  updateExpire(e) {
    if (e && e.target) {
      this.expire = e.target.value;
    }
  }
  updateQuality(e) {
    if (e && e.target) {
      this.quality = e.target.value;
    }
  }

  updateGuarantee(e) {
    if (e && e.target) {
      this.guarantee = e.target.value;
    }
  }

  updateTags(e) {
    if (e && e.target) {
      this.tags = e.target.value;
    }
  }

  updateDeadline(e) {
    if (e && e.target) {
      this.deadline = e.target.value;
      if (this.deadline.length == 0) {
        this.deadline = 5;
      }
    }
  }
  updateTitle(e) {
    if (e && e.target) {
      this.title = e.target.value;
    }
  }

  updateAddress(e) {
    if (e && e.target) {
      this.address = e.target.value;
      this.updateAddressBlock()
    }
  }

  updateContact(e) {
    if (e && e.target) {
      this.contact = e.target.value;
      this.updateAddressBlock()
    }
  }
  updatePhone(e) {
    if (e && e.target) {
      this.phone = e.target.value;
      this.updateAddressBlock()
    }
  }

  updateAddressBlock() {
    let clear = { contract: this.contact, address: this.address, phone: this.phone };
    clear = JSON.stringify(clear);
    let pk = this.state.productDetail.seller;
    let addressBlock = shareEncrypt(this.secret, pk, this.state.product.transactionId, clear);
    let addressHash = ''
    this.setState({ addressBlock: addressBlock, addressHash: addressHash });
  }



  doJoin() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    let data = {
      action: 'transaction', type: 'sjoin', amount: this.joinAmount,
      fee: this.fee, price: this.price, dealine: this.deadline, sid: transactionId, pk: peer.pk, accountId: peer.aid
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: 'success(),().', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '', renderDesposit: true })
        } else
          this.setState({ dialogMsg: 'err()' })
      }
    }).bind(this))
  }

  publishProduct() {
    let product = {
      tags: this.tags, title: this.title,//
      address: '', content: this.state.content,//
      quality: 0
    };
    let peer = createPeerId(this.secret);
    let str = JSON.stringify(product)
    let jsonHex = new Buffer(str).toString('base64');
    let data = {
      action: 'transaction', type: 'shopshare', productJson: jsonHex, guarantee: this.guarantee,
      fee: this.fee, price: this.price, expire: this.expire, dealine: this.deadline, pk: peer.pk,
      accountId: peer.aid, productDetail: null
    };
    //
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        //this.togglePublishProduct()
        let url = getCurrentHost() + "/#/bshop/product-list#" + msg.transactionId;
        this.setState({ dialogMsg: 'Success(),().', success: true })

      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '', renderDesposit: true })
        } else
          this.setState({ dialogMsg: 'err()' })
      }
    }).bind(this))
  }

  handleChange(value) {
    this.setState({ content: value })
  }

  rederAddProductDialog() {
    return (<Modal isOpen={this.state.addProduct} toggle={this.togglePublishProduct.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.togglePublishProduct.bind(this)}>Sell()</ModalHeader>
      <ModalBody>
        <FormGroup>
          <div className="controls">
            <BtbSecret onSecret={this.onSecret.bind(this)} />
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>()Title:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateTitle.bind(this)} placeholder='Title ' />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>()Tags:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateTags.bind(this)} placeholder='tags ' />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Price():</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput3" size="16" type="text" onChange={this.updatePrice.bind(this)} placeholder='price10BTB' />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='Guarantee():' content='5BTB10.' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput3" size="16" type="text" onChange={this.updateGuarantee.bind(this)} placeholder='' />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='Fee():' content='BTB,' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateFee.bind(this)} placeholder='miner Fee 1' />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id='dl4545' text='Deadline():' content='' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" onChange={this.updateDeadline.bind(this)} type="text" placeholder='5' />
              <InputGroupAddon addonType="append">
                <InputGroupText>minutes()</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">Details():</Label>
          <Alert><small>.</small></Alert>
          <ReactQuill value={this.state.content}
            modules={this.modules}
            onChange={this.handleChange.bind(this)} />
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.togglePublishProduct.bind(this)}>Close()</Button>
        <Button color="danger" hidden={this.state.success} onClick={
          ((e) => {
            let dialogMsg = "Working...(,)";
            this.setState({ dialogMsg })
            setTimeout(() => {
              this.publishProduct()
            }, 200);
          }).bind(this)
        }>Publish()</Button>
      </ModalFooter>
    </Modal>)
  }

  renderdialogMsg() {
    if (this.state.renderDesposit) {
      return (<div>
        <Label>{this.state.dialogMsg}</Label>
        <DepositDialog />
      </div>)
    } else
      if (this.state.dialogMsg) {
        return (<Label>{this.state.dialogMsg}</Label>)
      }
    return null;
  }

  setFilter(idx) {
    this.setState({ page: 1, activeTab: idx, filterOption: this.filterMsg[idx] })
  }

  upateFilterValue(e) {
    if (e && e.target) {
      let value = e.target.value;
      if (value.length > 0) {
        value = new Buffer(value).toString('base64')
        this.setState({ filterValue: value })
      } else {
        loadProductList(this.onProductList.bind(this), 1, this.state.activeTab)
      }
    }
  }

  updateJoinAmount(e) {
    if (e && e.target) {
      this.joinAmount = e.target.value;
    }
  }

  renderFilterOptions() {
    let ops = this.filterOptions.map((op, index) => {
      return (<DropdownItem onClick={(e) => {
        this.setFilter(index)
      }}>{op}</DropdownItem>)
    })
    return ops;
  }

  joinProduct(product) {
    this.setState({ product })
    this.toggleJoin()
  }

  showDetailDialog(product) {
    this.setState({ product })
    this.toggleDetail()
    setTimeout((() => {
      loadProductDetails(product, (ok, productDetail) => {
        if (ok && productDetail) {
          this.setState({ productDetail })
          let joins = productDetail.joins;
          if (joins)
            this.setState({ joins })
        }
      })
    }).bind(this), 500);
  }


  preJoinsPage() {
    if (this.state.joinsPage > 1) {
      this.state.joinsPage--
    }
    loadJoinTransactions(this.state.product, this.state.joinsPage, 6, (ok, joins) => {
      if (ok && joins)
        this.setState({ joins })
    })
  }

  nextJoinsPage() {
    this.state.joinsPage++
    loadJoinTransactions(this.state.product, this.state.joinsPage, 6, (ok, joins) => {
      if (ok && joins)
        this.setState({ joins })
    })
  }

  onDetailTabClicked(tab) {
    if (this.state.detailTab !== tab) {
      this.setState({
        detailTab: tab,
      });
      //
      if (tab === '0') {
        //load details
      }
      if (tab === '1') {
        //load joins transaction
        loadJoinTransactions(this.state.product, this.state.joinsPage, 6, (ok, joins) => {
          if (ok && joins)
            this.setState({ joins })
        })
      } if (tab === '2') {
        //load joins transaction
        loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
          if (ok && issues)
            this.setState({ issues })
        })
      }
    }
  }

  renderJoinTransactions() {
    let joins = this.state.joins;
    if (!joins)
      return null;
    let content = joins.map(((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()

      let info = res.winner ? <span style={{ color: 'red' }}>YES</span> : 'NO';
      if (this.state.product.status > 2) {
        info = res.winner ? <span style={{ color: 'red' }}>Winner()</span> : 'Refunded()';
        if (this.state.product.status == 11) {
          info = 'Refunded()';
        }
      }
      return (<tr>
        <td>{info}</td>
        <td><AccountDialog rsId={res.accountId} /></td>
        <td>{res.amount}BTB</td>
        <td>{res.fee}BTB</td>
        <td>{btime}</td>
      </tr>)
    }).bind(this));

    return content;
  }

  renderProductTabs() {
    if (this.state.detailTab === '0') {
      //quill
      let content;
      if (this.state.productDetail.product.json && this.state.productDetail.product.json.content)
        content = this.state.productDetail.product.json.content;
      if (content)
        return (<ReactQuill value={content} />)
    }
    if (this.state.detailTab === '1') {
      //join transactions
      return (<div><Table hover bordered striped responsive size="sm">
        <thead>
          <tr>
            <th>Winner()</th>
            <th>Buyer()</th>
            <th>Amount()</th>
            <th>Fee()</th>
            <th>Time()</th>
          </tr>
        </thead>
        <tbody>
          {this.renderJoinTransactions()}
        </tbody>
      </Table>
        <Pagination>
          <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.preJoinsPage() }}>-</PaginationLink></PaginationItem>
          <PaginationItem active>
            <PaginationLink tag="button">{this.state.joinsPage}</PaginationLink>
          </PaginationItem>
          <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextJoinsPage() }}>+</PaginationLink></PaginationItem>
        </Pagination>
      </div>)
    }
    if (this.state.detailTab === '2') {
      //issue transactions
      return (<Table hover bordered striped responsive size="sm">
        <thead>
          <tr>
            <th>Account()</th>
            <th>Buyer(%)</th>
            <th>Seller(%)</th>
            <th>Fee()</th>
            <th>Time()</th>
            <th>Note()</th>
          </tr>
        </thead>
        <tbody>
          {this.renderIssueTransactions()}
        </tbody>
        <Pagination>
          <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.preIssuePage() }}>-</PaginationLink></PaginationItem>
          <PaginationItem active>
            <PaginationLink tag="button">{this.state.issuePage}</PaginationLink>
          </PaginationItem>
          <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextIssuePage() }}>+</PaginationLink></PaginationItem>
        </Pagination>
      </Table>)
    }
  }

  preIssuePage() {
    this.state.issuePage--
    loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
      if (ok && issues)
        this.setState({ issues })
    })
  }

  nextIssuePage() {
    this.state.issuePage++
    loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
      if (ok && issues)
        this.setState({ issues })
    })
  }

  renderIssueTransactions() {
    let issues = this.state.issues;
    if (!issues)
      return null;
    let content = issues.map(((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()
      //
      let info = '';
      if (res.info)
        info = new Buffer(res.info, 'base64').toString()
      return (<tr>
        <td><AccountDialog rsId={res.from} /></td>
        <td>{res.wp}%</td>
        <td>{res.sp}%</td>
        <td>{res.fee}</td>
        <td>{btime}</td>
        <td>{info}</td>
      </tr>)
    }).bind(this));

    return content;
  }

  renderProductDetail() {
    if (!this.state.productDetail)
      return null;
    let count = this.state.productDetail.joinsCount;

    return (
      <div><Nav tabs>
        <NavItem>
          <NavLink
            className={classnames({ active: this.state.detailTab === '0' })}
            onClick={() => { this.onDetailTabClicked('0'); }}
          >
            <i className="icon-calculator"></i><Badge
              color="success">Info() </Badge>
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={classnames({ active: this.state.detailTab === '1' })}
            onClick={() => { this.onDetailTabClicked('1'); }}
          >
            <i className="icon-basket-loaded"></i><Badge pill color="danger">Join()-({count})</Badge>
          </NavLink>
        </NavItem>
        {this.renderIssueVoteTab()}
      </Nav>
        {
          this.renderProductTabs()
        }
      </div>)
  }

  renderIssueVoteTab() {
    let count = this.state.productDetail.issues.count;
    if (this.state.product.issue) {
      return (<NavItem>
        <NavLink
          className={classnames({ active: this.state.detailTab === '2' })}
          onClick={() => { this.onDetailTabClicked('2'); }}
        >
          <i className="icon-basket-loaded"></i><Badge pill color="danger">Votes()-({count})</Badge>
        </NavLink>
      </NavItem>)
    }
    return null;
  }


  renderPay10pbutton() {
    if (!this.state.productDetail)
      return null;
    let winner = this.state.productDetail.winner;
    if (!winner)
      return null;
    return (<ListGroupItem>
      <PopHint id="wind123" text={'Winner(): (' + winner.amount + 'BTB)'} content='' />
      <div><AccountDialog rsId={winner.accountId} /></div>
      <ActionButton product={this.state.product} productDetail={this.state.productDetail} winner={this.state.productDetail.winner} />
    </ListGroupItem>)
  }

  renderSteps() {
    if (true)
      return null;
    return (<Breadcrumb>
      <BreadcrumbItem><a href="#">Home</a></BreadcrumbItem>
      <BreadcrumbItem><a href="#">Library</a></BreadcrumbItem>
      <BreadcrumbItem active>Data</BreadcrumbItem>
    </Breadcrumb>)
  }

  renderIssueVoteList() {
    if (!this.state.productDetail || !this.state.productDetail.issues) {
      return null;
    }
    let count = this.state.productDetail.issues.count;
    let sp = this.state.productDetail.issues.result.sp;
    let wp = this.state.productDetail.issues.result.wp;
    let voter = this.state.productDetail.issues.result.voter;
    if (this.state.product.issue) {
      return (<div>
        <ListGroupItem><PopHint id='vw123' text='Vote Winner()' content='+90%10%' /> <AccountDialog rsId={voter} /> </ListGroupItem>
        <ListGroupItem>Vote Count() {count}</ListGroupItem>
        <ListGroupItem>Vote() {sp}%</ListGroupItem>
        <ListGroupItem>Vote() {wp}%</ListGroupItem>
      </div>);
    }
    return null;
  }

  renderProductDetailDialog() {
    if (!this.state.product)
      return null;
    let btime;
    let d = new Date();
    if (this.state.productDetail) {
      d.setTime(this.state.productDetail.product.createTime)
      btime = d.toLocaleString()
    }
    let dueTime = ''
    if (this.state.product.status == 2) {
      if (this.state.productDetail && this.state.productDetail.product.due)
        dueTime = ' ' + this.state.productDetail.product.due + ''
    }
    //
    return (<Modal isOpen={this.state.detail} toggle={this.toggleDetail.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleDetail.bind(this)}>Product details()</ModalHeader>
      <ModalBody>
        <ListGroup>
          <ListGroupItem>Name () {this.state.product.title}</ListGroupItem>
          <ListGroupItem>Owner()<AccountDialog rsId={this.state.product.senderId} /></ListGroupItem>
          <ListGroupItem>Time () {btime}</ListGroupItem>
          <ListGroupItem>Tags(){this.state.product.tags}</ListGroupItem>
          <ListGroupItem>Price(){this.state.product.price} BTB (){(this.state.product.price / 10)} BTB </ListGroupItem>
          <ListGroupItem>Total()<PopHint id={'tamt'} text={this.state.product.amout + 'BTB'} content='310%90%' /> {dueTime}</ListGroupItem>
          <ListGroupItem>Guarantee(){this.state.product.guarantee} BTB</ListGroupItem>
          <ListGroupItem><PopoverItem mode='step' product={this.state.product} /></ListGroupItem>
          {this.renderPay10pbutton()}
          {this.renderIssueVoteList()}
          <ListGroupItem>{this.renderProductDetail()}</ListGroupItem>
        </ListGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleDetail.bind(this)}>Cancel()</Button>
        {this.renderJoinButton1()}
      </ModalFooter>
    </Modal>)
  }
  renderJoinButton1() {
    if (!this.state.product)
      return null;
    if (this.state.product.status > 2)
      return null;
    return (<Button color="danger" onClick={e => {
      this.toggleDetail()
      this.joinProduct(this.state.product)
    }}>Join()</Button>)
  }


  rederJoinProductDialog() {
    if (!this.state.product)
      return null;
    return (<Modal isOpen={this.state.join} toggle={this.toggleJoin.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleJoin.bind(this)}>Join Group()</ModalHeader>
      <ModalBody>
        <p>Name () {this.state.product.title}</p>
        <p>Price(){this.state.product.price} BTB</p>
        <p>(){(this.state.product.price / 10)} BTB</p>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">Account Secret(BTB):</Label>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='Amount():' content='22' /></InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateJoinAmount.bind(this)} placeholder='2' />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='Fee():' content='BTB,' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateFee.bind(this)} placeholder='miner Fee 1' />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id='dl4545' text='Deadline():' content='' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" onChange={this.updateDeadline.bind(this)} type="text" placeholder='5' />
              <InputGroupAddon addonType="append">
                <InputGroupText>minutes()</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Alert color="danger"><small></small></Alert>
          <Alert color="danger"><small>710%</small></Alert>
          <Alert color="danger"><small></small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleJoin.bind(this)}>Cancel()</Button>
        {this.renderActionButton()}
      </ModalFooter>
    </Modal>)
  }

  renderActionButton() {
    if (this.state.success)
      return null;
    return (<Button color="danger" onClick={this.doJoin.bind(this)}>Publish()</Button>);
  }

  renderJoinButton(res) {
    let debug = false
    if (debug) {
      return (<Button size="sm" className="btn-html5 btn-brand mr-1 mb-1" onClick={(e) => this.joinProduct(res)}><i className="fa fa-html5"></i><span>Join()</span></Button>);
    } else {
      let sts = getProductStatus(res.status)
      if (res.status > 2 || res.closed) {
        return (<Button size="sm" className="btn-tumblr btn-brand mr-1 mb-1" onClick={(e) => this.showDetailDialog(res)}><i className="fa fa-tumblr"></i><span>{sts}</span></Button>);
      } else
        return (<Button size="sm" className="btn-html5 btn-brand mr-1 mb-1" onClick={(e) => this.joinProduct(res)}><i className="fa fa-html5"></i><span>Join()</span></Button>);
    }
  }

  renderProductList() {
    let list = this.state.list;
    if (!list)
      return null;
    let content = list.map((res) => {
      let d = new Date();
      d.setTime(res.createTime)
      let btime = d.toLocaleString()
      return (<tr>
        <td>{this.renderJoinButton(res)}</td>
        <td><a href='#' onClick={(e) => this.showDetailDialog(res)}><span>{res.title}</span></a></td>
        <td><PopHint id={'pic' + res.productId} title={':' + res.price + 'BTB'} text={(res.price / 10) + ''} content={'' + res.price / 10 + 'BTB.BTB'} /></td>
        <td><PopHint id={'ta' + res.productId} text={res.amout + 'BTB'} content='310%90%' /></td>
        <td><PopoverItem product={res} /></td>
      </tr>)
    });

    return content;
  }


  renderList() {
    let list = this.state.list;
    if (!list)
      return null;
    return (<Row>
      <Col>
        <Card>
          <CardBody>
            <InputGroup>
              <Input placeholder='search ()' onChange={this.upateFilterValue.bind(this)} />
              <Button color="primary" onClick={e => this.filterBlocks()}>
                <i className="fa fa-search"></i>&nbsp;
                </Button>
            </InputGroup>
            <Table hover bordered striped responsive size="sm">
              <thead>
                <tr>
                  <th>Join()</th>
                  <th>Title()</th>
                  <th>Price()</th>
                  <th>Joined()</th>
                  <th>Link()</th>
                </tr>
              </thead>
              <tbody>
                {this.renderProductList()}
              </tbody>
            </Table>
            {this.rederAddProductDialog()}
            {this.rederJoinProductDialog()}
            {this.renderProductDetailDialog()}
            <nav>
              <Pagination>
                <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.prePage() }}>Prev()</PaginationLink></PaginationItem>
                <PaginationItem active>
                  <PaginationLink tag="button">{this.state.page}</PaginationLink>
                </PaginationItem>
                <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextPage() }}>Next()</PaginationLink></PaginationItem>
              </Pagination>
            </nav>
            <Button onClick={(e) => { this.togglePublishProduct() }} className="btn-openid btn-brand"><i className="fa fa-openid"></i><span>Sell()</span></Button>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  onTabClicked(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
      let aid = 0;
      if (this.state.account) {
        aid = this.state.account.aid
      }
      loadProductList(this.onProductList.bind(this), 1, tab, aid + '')
    }
  }

  render() {
    return (
      <div className="animated fadeIn">
        <Nav tabs>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '0' })}
              onClick={() => { this.onTabClicked('0'); }}
            >
              <i className="icon-calculator"></i> <span className={this.state.activeTab === '0' ? '' : 'd-none'}> All() </span>{'\u00A0'}<Badge
                color="success">All() </Badge>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '1' })}
              onClick={() => { this.onTabClicked('1'); }}
            >
              <i className="icon-basket-loaded"></i> <span
                className={this.state.activeTab === '1' ? '' : 'd-none'}>Lucky()</span>{'\u00A0'}<Badge pill color="danger">Shopping Cart()</Badge>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '2' })}
              onClick={() => { this.onTabClicked('2'); }}
            >
              <i className="icon-pie-chart"></i> <span className={this.state.activeTab === '2' ? '' : 'd-none'}>I'am Boss()</span><Badge pill color="info">Mine()</Badge>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '3' })}
              onClick={() => { this.onTabClicked('3'); }}
            >
              <i className="icon-pie-chart"></i> <span className={this.state.activeTab === '3' ? '' : 'd-none'}>Vote()</span><Badge pill color="warning">Issues()</Badge>
            </NavLink>
          </NavItem>
        </Nav>
        {this.renderList()}
      </div>

    );
  }
}


class PopoverItem extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      popoverOpen: false
    };
  }

  toggle() {
    this.setState({
      popoverOpen: !this.state.popoverOpen,
    });
  }

  renderSteps() {
    let id = 'steps-' + this.props.product.transactionId;
    let stepInfo = ''
    if (this.props.product.steps) {
      let ss = this.props.product.steps.split(';');
      let content = ss.map((step, idx) => {
        let res;
        if (step && step.length > 0) {
          res = getProductStatus(step);
        }
        if (!res)
          return null;
        return (<ListGroupItem>{(idx + 1) + "." + res}</ListGroupItem>);
      })
      stepInfo = content;
    }

    let nxt = getProductNextStep(this.props.product.status);
    if (nxt) {
      nxt = 'Next()-> ' + nxt;
    }
    return (
      <span>
        <Alert id={id} onClick={this.toggle}><small> Status() : {getProductStatus(this.props.product.status)}  </small> </Alert>
        <Alert onClick={this.toggle}><small> {nxt} </small> </Alert>
        <Popover placement='top' isOpen={this.state.popoverOpen} target={id} toggle={this.toggle}>
          <PopoverHeader>Status()</PopoverHeader>
          <PopoverBody>{stepInfo}</PopoverBody>
        </Popover>
      </span>
    )
  }

  render() {
    if ('step' === this.props.mode)
      return (<ListGroup size='sm'>
        {this.renderSteps()}
      </ListGroup>);
    let id = 'Popover-' + this.props.product.transactionId;
    let link = getCurrentHost() + "/#/bshop/product-list#" + this.props.product.transactionId + '#' + this.props.product.title + ' ' + (this.props.product.price / 10) + '.7';

    return (
      <span>
        <span className='alert-primary  fade show' id={id} onClick={this.toggle}>
          
        </span>
        <Popover placement='top' isOpen={this.state.popoverOpen} target={id} toggle={this.toggle}>
          <PopoverHeader>Share Link()</PopoverHeader>
          <PopoverBody>{link}</PopoverBody>
        </Popover>
      </span>
    );
  }
}

export default ProductList;



