import React, { Component } from 'react';
import { Button, Input, Table, CardHeader, Popover, PopoverBody, PopoverHeader } from 'reactstrap';
import { loadAccountTransactions, loadAutoTransactions } from '../../eshop/eshop.js'
import classnames from 'classnames';
import { FormGroup, InputGroupText, InputGroup, InputGroupAddon, Label, PaginationItem, PaginationLink, Pagination } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader, Alert, DropdownToggle, Dropdown, DropdownItem, DropdownMenu } from 'reactstrap'
import { getTransactionName, calcAccountId, createPeerId, saveUserSec, simpleHash, loadLocalAccount, getShareHost } from '../../ggv/js/gegeUtil'
import BtbSecret from '../BtbSecret/BtbSecret'
import AccountDialog from '../AccountDialog/AccountDialog'
import PopHint from '../PopHint/PopHint'
import DepositDialog from '../DepositDialog/DepositDialog'
const { loadProductList, doBShopTransaction, getIssueReason, loadProductDetails, loadIssueTransactions, shareDecrypt, shareEncrypt, loadEncryptedInfo } = require('../../eshop/eshop')

let host = getShareHost();
const get = require('simple-get')

class ActionButton extends Component {
  constructor(props) {
    super(props);
    this.state = {
      product: this.props.product, productDetail: this.props.productDetail,
      winner: this.props.winner, paying: false, confirm: false, day7refund: false,
      issue: false, drop: false, selectIssueAction: false, selectActionTitle: 'Select()',
      issueReason: 0, issues: [], issuePage: 1, renderDesposit: false
    };
    this.fee = '1'
    this.deadline = 5
  }

  componentDidMount() {
    let account = loadLocalAccount();
    if (account && account.secret) {
      this.secret = account.secret;
      this.account = account;
    }
  }

  onSecret(sec) {
    if (sec) {
      this.secret = sec
      let rsId = calcAccountId(this.userSec);
      this.setState({ rsId: rsId })
    }
  }

  togglePay10p() {
    this.setState({
      paying: !this.state.paying, dialogMsg: ''
    });
  }

  pay10P() {
    //this.toggleDetail()
    this.togglePay10p()
  }

  toggleDrop() {
    this.setState({
      drop: !this.state.drop, dialogMsg: ''
    });
  }

  toggleDecrypt() {

    if (!this.state.decrypt && this.account) {
      loadEncryptedInfo(this.state.product, this.account.rsId, ((ok, infos) => {
        if (ok && infos) {
          this.setState({ infoBlocks: infos })
        }
      }).bind(this));
    } else {
      this.setState({ decrypted: null })
    }
    //
    let dialogMsg = '';
    if (!this.account) {
      dialogMsg = ''
    }
    this.setState({
      decrypt: !this.state.decrypt, dialogMsg: dialogMsg
    });
  }

  toggleSending() {
    this.setState({
      sending: !this.state.sending, dialogMsg: ''
    });
  }

  toggleIssue() {
    this.setState({
      issue: !this.state.issue, dialogMsg: ''
    });
  }

  toggleVotes() {
    if (!this.state.votes) {
      loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
        if (ok && issues)
          this.setState({ issues })
      })
    }
    this.setState({
      votes: !this.state.votes, dialogMsg: ''
    });

  }

  toggleRejectConfirm() {
    this.setState({
      rejectConfirm: !this.state.rejectConfirm, dialogMsg: ''
    });
  }

  toggle7dayRefund() {
    this.setState({
      day7refund: !this.state.day7refund, dialogMsg: ''
    });
  }

  renderActionButtons() {
    let debug = false
    if (debug) {
      return (<div>
        <Button color="danger" onClick={this.debug.bind(this)}>debug</Button>
        <Button color="danger" onClick={this.pay10P.bind(this)}>Pay()</Button>
        <Button color="danger" onClick={this.toggleDrop.bind(this)}>Drop()</Button>
        <Button color="danger" onClick={this.toggleSending.bind(this)}>Deliver()</Button>
        <Button color="danger" onClick={this.toggle7dayRefund.bind(this)}>Refund(7)</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>Issue()</Button>
        <Button color="danger" onClick={this.toggleRejectConfirm.bind(this)}>Confirme()</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>Issue()</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>Issue Vote()</Button>
        <Button color="danger" onClick={this.toggleVotes.bind(this)}>Votes( )</Button>
        <Button color="danger" onClick={this.toggleConfirm.bind(this)}>Confirme()</Button>
        <Button color="danger" onClick={this.toggleRejectConfirm.bind(this)}>Confirme()</Button>
        <Button color="danger" onClick={this.toggleDecrypt.bind(this)}>Decrypt()</Button>
      </div>)
    }
    let status = this.state.product.status;
    if (status == 3)
      return (<div>
        <Button color="danger" onClick={this.pay10P.bind(this)}>Pay()</Button>
        <Button color="danger" onClick={this.toggleDrop.bind(this)}>Drop()</Button>
      </div>)
    if (status == 4)
      return (<div>
        <Button color="danger" onClick={this.toggleDecrypt.bind(this)}>Decrypt()</Button>
        <Button color="danger" onClick={this.toggleSending.bind(this)}>Deliver()</Button>
      </div>)
    if (status == 5)
      return (<div>
        <Button color="danger" onClick={this.toggleDecrypt.bind(this)}>Decrypt()</Button>
        <Button color="danger" onClick={this.toggleConfirm.bind(this)}>Confirme()</Button>
        <Button color="danger" onClick={this.toggle7dayRefund.bind(this)}>Refund(7)</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>Issue()</Button>
      </div>)
    if (status == 6)
      return (<div>
        <Button color="danger" onClick={this.toggleRejectConfirm.bind(this)}>Confirme()</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>Issue()</Button>
      </div>)
    if (status == 10)
      return (<div>
        <Button color="danger" onClick={this.toggleVotes.bind(this)}>Votes()</Button>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>Issue Vote()</Button>
        <Button color="danger" onClick={this.toggleConfirm.bind(this)}>Confirme()</Button>
        <Button color="danger" onClick={this.toggleRejectConfirm.bind(this)}>Confirme()</Button>
      </div>)
    return null;
  }

  updateFee(e) {
    if (e && e.target) {
      this.fee = e.target.value;
      if (this.fee.length == 0) {
        this.fee = 1;
      }
    }
  }

  updateSellerRefund(e) {
    if (e && e.target) {
      this.sellerRefund = e.target.value;
    }
  }

  updateIssueNote(e) {
    if (e && e.target) {
      this.issueNote = e.target.value;
    }
  }

  updateBuyerRefund(e) {
    if (e && e.target) {
      this.buyerRefund = e.target.value;
    }
  }
  
  updateDeadline(e) {
    if (e && e.target) {
      this.deadline = e.target.value;
      if (this.deadline.length == 0) {
        this.deadline = 5;
      }
    }
  }


  updateAddress(e) {
    if (e && e.target) {
      this.address = e.target.value;
      this.updateAddressBlock()
    }
  }

  updateProductAddress(e) {
    if (e && e.target) {
      this.address = e.target.value;
    }
  }
  updateContact(e) {
    if (e && e.target) {
      this.contact = e.target.value;
      this.updateAddressBlock()
    }
  }
  updatePhone(e) {
    if (e && e.target) {
      this.phone = e.target.value;
      this.updateAddressBlock()
    }
  }

  updateAddressBlock() {
    let clear = { contact: this.contact, address: this.address, phone: this.phone };
    clear = JSON.stringify(clear);
    let pk = this.state.productDetail.seller;
    let addressHash = simpleHash(clear);
    let addressBlock = shareEncrypt(this.secret, pk, this.state.product.transactionId, clear);
    this.setState({ addressBlock: addressBlock, addressHash: addressHash });
  }

  updateTracking(e) {
    if (e && e.target) {
      let tracking = e.target.value;
      //get winner pk
      let trackingHash = simpleHash(tracking);
      let pk = this.state.productDetail.winner.pk;
      let trackingBlock = shareEncrypt(this.secret, pk, this.state.product.transactionId, tracking);
      this.setState({ trackingBlock: trackingBlock, trackingHash: trackingHash });
    }
  }

  debug() {
    let pk = ''
    let nonce = ''
    let block = ''
    let clear = shareDecrypt(this.secret, pk, nonce, block);
    console.log(clear)
  }

  renderdialogMsg() {
    if (this.state.renderDesposit) {
      return (<div>
        <Label>{this.state.dialogMsg}</Label>
        <DepositDialog />
      </div>)
    } else
      if (this.state.dialogMsg) {
        return (<Label>{this.state.dialogMsg}</Label>)
      }
    return null;
  }


  doPaying() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    let amount = (this.state.product.price / 10);
    let hash = this.state.addressHash;
    let addressBlock = this.state.addressBlock;
    let data = {
      action: 'transaction', type: 'sAccept', amount: amount + '', hash: hash,
      fee: this.fee, dealine: this.deadline, sid: transactionId, pk: peer.pk, accountId: peer.aid,
      addressBlock: addressBlock
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: 'success(),()', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '', renderDesposit: true })
        } else
          this.setState({ dialogMsg: 'err()' })
      }
    }).bind(this))
  }

  do7dayrefund() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    let hash = ''
    let info = this.state.trackingBlock;
    info = new Buffer(info).toString('base64')
    let data = {
      action: 'transaction', type: 'sAction', hash: hash, info: info, accountId: peer.aid,
      fee: this.fee, dealine: this.deadline, sid: transactionId, saction: '20'
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: 'success(),()', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '', renderDesposit: true })
        } else
          this.setState({ dialogMsg: 'err()' })
      }
    }).bind(this))
  }


  doDrop() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    let hash = '0';
    let data = {
      action: 'transaction', type: 'sAction', hash: hash, accountId: peer.aid,
      fee: this.fee, dealine: this.deadline, sid: transactionId, saction: '4'
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: 'success(),()', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '', renderDesposit: true })
        } else
          this.setState({ dialogMsg: 'err()' })
      }
    }).bind(this))
  }

  doIssue() {
    if (this.state.issueReason == 0) {
      this.setState({ dialogMsg: 'Choose()' })
      return;
    }
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    // 1 not receive,2 not sent back.3 vote
    let reason = this.state.issueReason;
    let wp = this.buyerRefund;
    let sp = this.sellerRefund;
    let info = this.issueNote;
    let hash = 0;
    if (info && info.length > 0) {
      hash = simpleHash(info);
      info = new Buffer(info).toString('base64')
    }
    let data = {
      action: 'transaction', type: 'issue', hash: hash, info: info, pk: peer.pk,
      fee: this.fee, dealine: this.deadline, sid: transactionId, reason: reason,
      wp: wp, sp: sp
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: 'success(),()', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '', renderDesposit: true })
        } else
          this.setState({ dialogMsg: 'err()' })
      }
    }).bind(this))
  }

  doRejectConfirm() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    let data = {
      action: 'transaction', type: 'sAction', accountId: peer.aid,
      fee: this.fee, dealine: this.deadline, sid: transactionId, saction: '21'
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: 'success(),()', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '', renderDesposit: true })
        } else {
          if (msg && !msg.ok && '1001' === msg.code) {
            this.setState({ dialogMsg: '', renderDesposit: true })
          } else
            this.setState({ dialogMsg: 'err()' })
        }
      }
    }).bind(this))
  }

  doSending() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    let info = this.state.trackingBlock;
    let hash = this.state.trackingHash;
    info = new Buffer(info).toString('base64')
    let data = {
      action: 'transaction', type: 'sAction', accountId: peer.aid,
      fee: this.fee, dealine: this.deadline, sid: transactionId, saction: '2', info: info, hash
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: 'success(),()', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '', renderDesposit: true })
        } else
          this.setState({ dialogMsg: 'err()' })
      }
    }).bind(this))
  }

  doWinnerConfirm() {
    let transactionId = this.state.product.transactionId;
    let peer = createPeerId(this.secret);
    //check peer is winner
    let data = {
      action: 'transaction', type: 'sAction', accountId: peer.aid,
      fee: this.fee, dealine: this.deadline, sid: transactionId, saction: '3'
    };
    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: 'success(),()', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '', renderDesposit: true })
        } else
          this.setState({ dialogMsg: 'err()' })
      }
    }).bind(this))
  }

  toggleConfirm() {
    this.setState({
      confirm: !this.state.confirm, dialogMsg: ''
    });
  }

  renderDropDialog() {
    let winnerId = this.state.winner.accountId
    return (<Modal isOpen={this.state.drop} toggle={this.toggleDrop.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleDrop.bind(this)}>{winnerId} Drop()</ModalHeader>
      <ModalBody>
        <p>Name () {this.state.product.title}</p>
        <p>Price(){this.state.product.price} BTB</p>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline()}
        <FormGroup>
          <Alert color="danger"><small>10%,10%</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleDrop.bind(this)}>Cancel()</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.doDrop.bind(this)}>Drop()</Button>
      </ModalFooter>
    </Modal>)
  }

  renderRejectConfirmDialog() {
    let senderId = this.state.product.senderId;
    return (<Modal isOpen={this.state.rejectConfirm} toggle={this.toggleRejectConfirm.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleRejectConfirm.bind(this)}>{senderId} Confirm()</ModalHeader>
      <ModalBody>
        <p>Name () {this.state.product.title}</p>
        <p>Price(){this.state.product.price} BTB</p>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline()}
        <FormGroup>
          <Alert color="danger"><small>10%,10%.</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleRejectConfirm.bind(this)}>Cancel()</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.doRejectConfirm.bind(this)}>Confirmed()</Button>
      </ModalFooter>
    </Modal>)
  }

  renderIssueTransactions() {
    let issues = this.state.issues;
    if (!issues)
      return null;
    let content = issues.map(((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()
      return (<tr>
        <td><AccountDialog rsId={res.from} /></td>
        <td>{res.wp}%</td>
        <td>{res.sp}%</td>
        <td>{res.fee}</td>
        <td>{new Buffer(res.info, 'base64').toString()}</td>
        <td>{btime}</td>
      </tr>)
    }).bind(this));

    return content;
  }

  renderIssueVotes() {
    let winnerId = this.state.winner.accountId
    let senderId = this.state.product.senderId;
    return (<Modal isOpen={this.state.votes} toggle={this.toggleVotes.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleVotes.bind(this)}>Votes()</ModalHeader>
      <ModalBody>
        <p>Name () {this.state.product.title}</p>
        <p>Price(){this.state.product.price} BTB</p>
        <p>Seller()<AccountDialog rsId={senderId} /></p>
        <p>Winner()<AccountDialog rsId={winnerId} /></p>
        <p>Issue(){getIssueReason(this.state.product.issueReason)}</p>
        <Table hover bordered striped responsive size="sm">
          <thead>
            <tr>
              <th>Account()</th>
              <th>Buyer(%)</th>
              <th>Seller(%)</th>
              <th>Fee()</th>
              <th>Note()</th>
              <th>Time()</th>
            </tr>
          </thead>
          <tbody>
            {this.renderIssueTransactions()}
          </tbody>
          <Pagination>
            <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.preIssuePage() }}>-</PaginationLink></PaginationItem>
            <PaginationItem active>
              <PaginationLink tag="button">{this.state.issuePage}</PaginationLink>
            </PaginationItem>
            <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextIssuePage() }}>+</PaginationLink></PaginationItem>
          </Pagination>
        </Table>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleVotes.bind(this)}>Cancel()</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.toggleIssue.bind(this)}>Vote()</Button>
      </ModalFooter>
    </Modal>)
  }


  preIssuePage() {
    this.state.issuePage--
    loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
      if (ok && issues)
        this.setState({ issues })
    })
  }

  nextIssuePage() {
    this.state.issuePage++
    loadIssueTransactions(this.state.product, this.state.issuePage, 6, (ok, issues) => {
      if (ok && issues)
        this.setState({ issues })
    })
  }


  renderWinnerConfirmedProductDialog() {
    let winnerId = this.state.winner.accountId
    return (<Modal isOpen={this.state.confirm} toggle={this.toggleConfirm.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleConfirm.bind(this)}>{winnerId} Confirm()</ModalHeader>
      <ModalBody>
        <p>Name () {this.state.product.title}</p>
        <p>Price(){this.state.product.price} BTB</p>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline()}
        <FormGroup>
          <Alert color="danger"><small>.</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleConfirm.bind(this)}>Cancel()</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.doWinnerConfirm.bind(this)}>Confirmed()</Button>
      </ModalFooter>
    </Modal>)
  }

  renderIssueDialog() {
    let winnerId = this.state.winner.accountId
    let senderId = this.state.product.senderId;
    let totalAmount = (this.state.product.price + " + " + this.state.product.guarantee)
    let t1 = ''
    let t2 = ''
    let t3 = ''
    //
    return (<Modal isOpen={this.state.issue} toggle={this.toggleIssue.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleIssue.bind(this)}>Issue()</ModalHeader>
      <ModalBody>
        <p>Name () {this.state.product.title}</p>
        <p>Price(){this.state.product.price} BTB</p>
        <p>Price(){this.state.product.guarantee} BTB</p>
        <p>Seller()<AccountDialog rsId={senderId} /></p>
        <p>Winner()<AccountDialog rsId={winnerId} /></p>
        <p>Issue(){getIssueReason(this.state.product.issueReason)}</p>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Dropdown isOpen={this.state.selectIssueAction} toggle={
            (() => {
              this.setState({
                selectIssueAction: !this.state.selectIssueAction,
              });
            }).bind(this)
          }>
            <DropdownToggle caret>
              {this.state.selectActionTitle}
            </DropdownToggle>
            <DropdownMenu>
              <DropdownItem onClick={(() => {
                this.setState({ selectActionTitle: t1, issueReason: 1 })
              }).bind(this)}>{t1}</DropdownItem>
              <DropdownItem onClick={(() => {
                this.setState({ selectActionTitle: t2, issueReason: 2 })
              }).bind(this)}>{t2}</DropdownItem>
              <DropdownItem onClick={(() => {
                this.setState({ selectActionTitle: t3, issueReason: 3 })
              }).bind(this)}>{t3}</DropdownItem>
            </DropdownMenu>
          </Dropdown>
          <Label htmlFor="appendedInputButtons">( {totalAmount} 90%10%):</Label>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Seller():</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateSellerRefund.bind(this)} placeholder='' />
              <InputGroupAddon addonType="append">
                <InputGroupText>%()</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Buyer():</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateBuyerRefund.bind(this)} placeholder='' />
              <InputGroupAddon addonType="append">
                <InputGroupText>%()</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>

        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>note():</InputGroupText>
              </InputGroupAddon>
              <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" onChange={this.updateIssueNote.bind(this)} placeholder='' />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline()}
        <FormGroup>
          <Alert color="danger"><small></small></Alert>
          <Alert color="danger"><small>10%</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleIssue.bind(this)}>Cancel()</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.doIssue.bind(this)}>Sent()</Button>
      </ModalFooter>
    </Modal>)
  }


  renderWinner7dayRefundDialog() {
    let winnerId = this.state.winner.accountId
    return (<Modal isOpen={this.state.day7refund} toggle={this.toggle7dayRefund.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggle7dayRefund.bind(this)}>{winnerId} - Reject(7)</ModalHeader>
      <ModalBody>
        <p>Name () {this.state.product.title}</p>
        <p>Price(){this.state.product.price} BTB</p>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">Account Secret(BTB):</Label>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline()}
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Tracking NO.():</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateTracking.bind(this)} placeholder='Tracking NO.():' />
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Info Block():</InputGroupText>
              </InputGroupAddon>
              <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" value={this.state.trackingBlock} />
              <Alert color="danger"><small></small></Alert>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Alert color="danger"><small>7.</small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggle7dayRefund.bind(this)}>Cancel()</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.do7dayrefund.bind(this)}>Reject()</Button>
      </ModalFooter>
    </Modal>)
  }

  renderFeeAndDeadline() {
    return (<div>
      <FormGroup>
        <div className="controls">
          <InputGroup className="input-prepend">
            <InputGroupAddon addonType="prepend">
              <InputGroupText><PopHint id="min123" text='Fee():' content='BTB,' /></InputGroupText>
            </InputGroupAddon>
            <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateFee.bind(this)} placeholder='miner Fee 1' />
            <InputGroupAddon addonType="append">
              <InputGroupText>BTB</InputGroupText>
            </InputGroupAddon>
          </InputGroup>
        </div>
      </FormGroup>
      <FormGroup>
        <div className="controls">
          <InputGroup className="input-prepend">
            <InputGroupAddon addonType="prepend">
              <InputGroupText><PopHint id='dl4545' text='Deadline():' content='' /></InputGroupText>
            </InputGroupAddon>
            <Input id="appendedPrependedInput" size="16" onChange={this.updateDeadline.bind(this)} type="text" placeholder='5' />
            <InputGroupAddon addonType="append">
              <InputGroupText>minutes()</InputGroupText>
            </InputGroupAddon>
          </InputGroup>
        </div>
      </FormGroup>

    </div>)
  }


  doDecrypt() {
    if (!this.state.infoBlocks)
      return null;
    if (!this.secret) {
      this.setState({ dialogMsg: 'need login()' })
      return;
    }
    let decrypted = [];
    this.state.infoBlocks.forEach(((e) => {
      let clear;
      if (e.type > 0)
        clear = shareDecrypt(this.secret, e.pk, e.id, e.block);
      else
        clear = new Buffer(e.block, 'base64').toString()
      decrypted.push(clear);
    }).bind(this))
    this.setState({ decrypted })
  }

  renderEncryptedInfos() {
    if (!this.state.infoBlocks)
      return null;
    let idx = 0;
    let content = this.state.infoBlocks.map(((rec) => {
      let txt = rec.block;
      if (this.state.decrypted) {
        txt = this.state.decrypted[idx];
      }
      idx++;
      return (<div className="controls">
        <InputGroup className="input-prepend">
          <InputGroupAddon addonType="prepend">
            <InputGroupText>Block():</InputGroupText>
          </InputGroupAddon>
          <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" value={txt} />
        </InputGroup>
      </div>)
    }).bind(this))
    return content;
  }





  renderDecryptDialog() {
    return (<Modal isOpen={this.state.decrypt} toggle={this.toggleDecrypt.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleDecrypt.bind(this)}> Decrypt()</ModalHeader>
      <ModalBody>
        <p>Name () {this.state.product.title}</p>
        <p>Price(){this.state.product.price} BTB</p>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          {this.renderEncryptedInfos()}
        </FormGroup>
        <FormGroup>
          <Alert color="danger"><small></small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleDecrypt.bind(this)}>Cancel()</Button>
        <Button color="danger" onClick={this.doDecrypt.bind(this)}>Decrypt()</Button>
      </ModalFooter>
    </Modal>)
  }

  renderDeliverProductDialog() {
    let senderId = this.state.product.senderId;
    return (<Modal isOpen={this.state.sending} toggle={this.toggleSending.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleSending.bind(this)}>{senderId} Deliver()</ModalHeader>
      <ModalBody>
        <p>Name () {this.state.product.title}</p>
        <p>Price(){this.state.product.price} BTB</p>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">Account Secret(BTB):</Label>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline()}
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Tracking NO.():</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateTracking.bind(this)} placeholder='Tracking NO.():' />
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Info Block():</InputGroupText>
              </InputGroupAddon>
              <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" value={this.state.trackingBlock} />
              <Alert color="danger"><small></small></Alert>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Alert color="danger"><small></small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleSending.bind(this)}>Cancel()</Button>
        <Button color="danger" hidden={this.state.success} onClick={this.doSending.bind(this)}>Deliver()</Button>
      </ModalFooter>
    </Modal>)
  }

  renderPayingProductDialog() {
    if (!this.state.product)
      return null;
    let winner = this.state.winner;
    let pwdHint = '';
    let payId = ''
    if (winner) {
      pwdHint = "" + winner.accountId + "" + 'Type ' + winner.accountId + "'s secret."
      payId = winner.accountId
    }

    return (<Modal isOpen={this.state.paying} toggle={this.togglePay10p.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.togglePay10p.bind(this)}>{payId} Pay()</ModalHeader>
      <ModalBody>
        <p>Name () {this.state.product.title}</p>
        <p>Price(){this.state.product.price} BTB</p>
        <p>Guarantee(){this.state.product.guarantee} BTB</p>
        <p>(){(this.state.product.price / 10)} BTB</p>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">Account Secret(BTB):</Label>
          <div className="controls">
            <InputGroup>
              <BtbSecret onSecret={this.onSecret.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderFeeAndDeadline()}
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>contact():</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateContact.bind(this)} placeholder='contact()' />
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>phone():</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updatePhone.bind(this)} placeholder='phone()' />
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Address():</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateAddress.bind(this)} placeholder='Address()' />
            </InputGroup>
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Address Block():</InputGroupText>
              </InputGroupAddon>
              <Input type="textarea" name="textarea-input" id="textarea-input" rows="3" value={this.state.addressBlock} />
              <Alert color="danger"><small></small></Alert>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Alert color="danger"><small>{this.state.product.price}BTB </small></Alert>
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.togglePay10p.bind(this)}>Cancel()</Button>
        {this.renderPayActionButton()}
      </ModalFooter>
    </Modal>)
  }

  renderPayActionButton() {
    if (this.state.success)
      return null;
    return (<Button color="danger" onClick={this.doPaying.bind(this)}>Pay()</Button>);
  }

  render() {
    return (<div>
      {this.renderActionButtons()}
      {this.renderPayingProductDialog()}
      {this.renderDropDialog()}
      {this.renderDeliverProductDialog()}
      {this.renderWinnerConfirmedProductDialog()}
      {this.renderWinner7dayRefundDialog()}
      {this.renderIssueDialog()}
      {this.renderRejectConfirmDialog()}
      {this.renderIssueVotes()}
      {this.renderDecryptDialog()}
    </div>)
  }
}
export default ActionButton;


